.jumbotron {
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: transparent;
}
.container {
    height: 100%;
    color: white;
    font-family: "Open Sans", sans-serif;
}
.title {
    font-size: 5em;
    max-width: 60%;
    text-align: center;
}
.subTitle {
    font-size: 2em;
    max-width: 60%;
    text-align: center;
    margin-top: 1em;
}


.choosingImage {
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 1em;
}

